var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('project-template',{staticClass:"project-vue",scopedSlots:_vm._u([{key:"summaryImage",fn:function(){return [_c('vuelogo')]},proxy:true},{key:"summaryText",fn:function(){return [_c('p',[_vm._v("\n            I wrote this website you're viewing now using Vue and Sass as a\n            learning experience. The biggest thing I wanted to learn was Vue\n            since I came from an AngularJS background. For this reason, I\n            wrote most of it from scratch so I could learn the ins and outs\n            of Vue.\n            "),_c('a',{attrs:{"href":"https://github.com/nate-h/nhapeman.com_v3"}},[_vm._v("\n                Check out the code here!\n            ")]),_vm._v("\n            In addition to wanting to learn Vue, I also wanted to take the\n            time and learn some of things I didn't have time to learn on the\n            job. As a software developer, you constantly have to fight\n            deadlines and sadly this means you have to occasionally put\n            aside learning something to meet a deadline. The last thing I\n            hoped to learn while developing this website was new patterns\n            from using the\n            "),_c('a',{attrs:{"href":"https://en.wikipedia.org/wiki/Don%27t_repeat_yourself"}},[_vm._v("DRY principle")]),_vm._v("\n            wherever possible. So while developing this website, I rewrote\n            many things as I learned cooler things Vue had to offer.\n        ")])]},proxy:true},{key:"demo",fn:function(){return [_c('vuelogo')]},proxy:true},{key:"demoDetails",fn:function(){return [_c('p',[_vm._v("\n            My experience with Vue so far has been completely positive,\n            unlike my experience with AngularJS which could have been\n            better. I suspected that the documentation for AngularJS was bad\n            but it wasn't until I dove into Vue that I realized what I was\n            missing. The best way to read AngularJS documentation is to\n            ignore it completly and use stackoverflow. I found it way too\n            complex for describing simple concepts. It also uses other\n            AngularJS terms to define something. So You almost need an\n            understanding of everything else to understand one thing.\n        ")]),_c('p',[_vm._v("\n            Vue on the other hand was much easier to learn. I'll admit that\n            part of the reason why it was easier to learn is that so many\n            things were borrowed from AngularJS. So naturally it wasn't too\n            hard to translate a lot of my knowledge over from AngularJS. I\n            think if I didn't have a background in AngularJS or any web\n            framework for that matter, Vue would have been easy to learn\n            because it's well designed and has documentation geared towards\n            newbies. I think one of the reasons why Vue became the framework\n            it is today is that it's mostly written by one dude,\n            "),_c('a',{attrs:{"href":"https://twitter.com/youyuxi?lang=en"}},[_vm._v("Evan You")]),_vm._v(" so\n            perhaps it doesn't suffer from having\n            "),_c('a',{attrs:{"href":"https://www.youtube.com/watch?v=QrGrOK8oZG8"}},[_vm._v("too many cooks")]),_vm._v(".\n        ")]),_c('p',[_vm._v("\n            Working with it so far, I find that it has a good balance of\n            giving the developer some freedom while still requiring some\n            order in places that create cleaner more manageable code. An\n            example of this are the properties you can define when creating\n            a component. These properties like watch, props, methods and\n            data provide a formal place to group similar items. Vue also\n            provides life cycle hooks that allow you to do stuff at the\n            precise time in the component creation. AngularJS has some\n            formal ways to group common functionality and even provides some\n            life cycle hooks but it's very minimal. Looking back at most of\n            the controllers I wrote, they all would have benefitted by\n            having one place to define the watchers or one place to define\n            scope variables. I still developed my own standards to attempt\n            to have that behavior but it would have been much easier/\n            cleaner if the standard was built into the framework.\n        ")]),_c('p',[_vm._v("\n            Another great feature Vue has is the ability to define a\n            component in one file. These\n            "),_c('a',{attrs:{"href":"https://vuejs.org/v2/guide/single-file-components.html"}},[_vm._v("\n                single file components")]),_vm._v("\n            simply have the Javascript, CSS, and HTML stuffed into 1 file as\n            opposed to 3 seperate files. The beauty of this feature is that\n            developers are more incentivised to break up their code into\n            smaller pieces. I find it annoying when you want to create a\n            small component but need to create 3 files for your css/js/html\n            that have 30 lines each. Being able to throw all 3 snippets into\n            one file makes it so: you're not overwhlemed by number of files,\n            you can bounce around the code faster since it's all in one\n            file, delete the component easier.\n        ")]),_c('p',[_vm._v("\n            The last thing I like about Vue is how easy it is to dive into\n            it. The first step of learning anything new is getting your foot\n            in the door and with the Vue CLI, it's that much easier to get\n            started with Vue. Using the client, it takes 4 lines to get a\n            local website up and running. And while configuring a new\n            project, you can choose between many popular options for things\n            like linters and testing frameworks. Get started today with:\n  